// pages/status.tsx
import { useState } from "react";

export default function StatusPage() {
  const [statuses, setStatuses] = useState<
    { id: number; text: string; time: string }[]
  >([]);
  const [newStatus, setNewStatus] = useState("");

  const handlePost = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newStatus.trim()) return;

    setStatuses([
      { id: Date.now(), text: newStatus, time: new Date().toLocaleString() },
      ...statuses,
    ]);
    setNewStatus("");
  };

  return (
    <div style={{ maxWidth: "600px", margin: "2rem auto" }}>
      <form
        onSubmit={handlePost}
        style={{ display: "flex", flexDirection: "column", gap: "0.5rem" }}
      >
        <textarea
          placeholder="Escreva atÃ© 140 caracteres..."
          maxLength={140}
          value={newStatus}
          onChange={(e) => setNewStatus(e.target.value)}
          rows={3}
          style={{
            padding: "0.5rem",
            borderRadius: "6px",
            border: "1px solid #ccc",
          }}
        />
        <button
          type="submit"
          style={{
            padding: "0.5rem",
            borderRadius: "6px",
            backgroundColor: "#eee",
            cursor: "pointer",
          }}
        >
          Publicar
        </button>
      </form>

      <div
        style={{
          marginTop: "1rem",
          display: "flex",
          flexDirection: "column",
          gap: "1rem",
        }}
      >
        {statuses.map((status) => (
          <div
            key={status.id}
            style={{
              padding: "1rem",
              border: "1px solid #ccc",
              borderRadius: "8px",
            }}
          >
            <p>{status.text}</p>
            <span style={{ fontSize: "0.8rem", color: "#666" }}>
              {status.time}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
}
